  .switcherCircleHorizontal {background-color:#333}

  .button1 {background:#333; border: 0px solid #333;}
  .button1:active{background: #ddd; border: 2px solid #333;} 

  .button2{background:#333; border:0px solid}
  .button2toggled {background:#ddd; border:2px solid #333}
  .tetherCircle{background-color: #333333;}
  .playButton {
    background:rgba(51,51,51);
    color: white
  }

  .canvasDiv {
    border: 3px solid #333;
 }

  .volumeSlider{
