  }

  .dividerLine {background-color:#ddd ;}

  .cube {background-color: #333;}
  .cubeContainer {background-color:rgba(51,51,51,0.2)}


  :root {
    --particle-color: var(--particle-color-dark);
    --canvas-colorelement: var(--canvas-colorelement-dark)
  }
