0px;\n    height:10px;\n    border:5px solid #333;\n    position:absolute;\n    bottom:1.4px;\n    border-top:0;\n    z-index:-1;\n}\n.navbarButton.active:after{\n    border-left:0;\n    border-radius: 0 0 10px 0;\n    left:-10px;\n}\n.navbarButton.active:before{\n    border-right:0;\n    border-radius: 0 0 0 10px;\n    right:-10px;\n}\n\n.buttonContainer{\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  height: 100%;\n}\n\n.buttonRow {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.buttonColumn{\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.button1 {\n  width: 100px;\n  height: 100px;\n  padding: 10px 20px; \n  border-radius: 50px;\n  margin:0.5rem;\n  border: 0px solid white;\n\n  background:rgba(255, 255, 255, 0.5);\n  transition: transform 0.3s ease, background 0.2s;\n}\n\n.button1:hover {\n  transform: scale(1.03);\n}\n\n.button1:active{\n  --scale-factor:0.95;\n  transform: scale(var(--scale-factor));\n  background: #333;\n  border: 2px solid rgba(255, 255, 255, 0.5)\n}\n\n.button2 {\n  width: 105px;\n  height: 105px;\n  padding: 20px; \n  border-radius: 40px;\n  margin:0.5rem;\n  border: 0;\n  background:rgba(255, 255, 255, 0.5);\n  transition: transform 0.3s ease, background 0.2s;\n}\n\n.button2toggled {\n  width: 105px;\n  height: 105px;\n  padding: 20px; \n  border-radius: 40px;\n  margin:0.5rem;\n  border: 2px solid rgba(255, 255, 255, 0.5);\n  background:#333;\n  transition: transform 0.3s ease, background 0.2s;\n}\n\n.button2:hover {\n  transform: scale(1.03);\n}\n\n.buttonInSettings{\n  margin-right: 0.25rem;\n}\n\n.logo{\n  display:flex;\n  justify-content: center;\n  align-items: center;\n  height:400px;\n}\n.logoImg {\n  width:400px;\n  height:400px;\n}\n\n.spinnerDiv {\n  position:relative;\n  margin-top:6rem;\n  margin-left:3rem;\n  margin-